.header-color {
  background: #b92b27;
  background: -webkit-linear-gradient(to right, #1565C0, #b92b27);  
  background: linear-gradient(to right, #1565C0, #b92b27);
}
.landing-grid {
  background: #ADA996;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #EAEAEA, #DBDBDB, #F2F2F2, #ADA996);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #EAEAEA, #DBDBDB, #F2F2F2, #ADA996); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  top: 0;
  left: 0;
  position: fixed;
  height: 100%;
  width: 100%;
  
}

.projects-grid {
  display: flex;
  position: fixed;
}

.img-inside {
  border-radius: 40%;
  height: 200px;
  width: 200px;
}

.avatar-img {
  height: 200px;
  width: 200px;
  margin-left: 50px;
  margin-top: 70px;
  padding-left: 650px;
  padding-right: 10px;
  margin-bottom: 50px;
}



.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  padding-top: 8px;
  border-radius: 10px;
  padding-bottom: 8px;
}

.banner-text h1 {
  font-size: 66px;
  
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color:white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color:white;
  font-size: 5em;
  padding: 8px;
}


/* Resume Page css */
.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

/* contact page css */

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: #2c3e50; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  );
  color: black;
  margin-bottom: 20px;
}
.contact-grid cell {
  background: #2c3e50; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #4ca1af,
    #2c3e50
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.contact-grid h2 {
  font-family: "Anton", sans-serif;
}

.contact-grid p {
  font-family: "Oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem
}
